import React, { useEffect, useState } from "react";
import { MdDelete } from "react-icons/md";
import { connect } from "react-redux";
import { positionRequirementInterface } from ".";
import { PushPopupAlert } from "../../actions";
import {
  getDocumentCategory,
  getDocumentFields,
  getDocumentSubCategory,
} from "../../actions/document";
import { getPositions } from "../../actions/settings";
import { PopupAlertData } from "../../components/Alert/Popup";
import Loading from "../../components/Loading/Loading";
import Table from "../../components/Table/Table";
import { course } from "../../interfaces/course";
import { education_category } from "../../interfaces/education_category";
import { school_level } from "../../interfaces/school_level";
import { StoreState } from "../../reducers";
import { DocumentReducerInterface } from "../../reducers/document";
import { capitalizeFirstLetter } from "../../shared/dataTest";
import {
  DocumentFieldsInterface,
  DocumentSubCategoryInterface,
  PositionRequirementsFieldInterface,
} from "../../shared/interfaces";
import AddRequirements from "./AddRequirements";

interface StepThreeProps {
  positionId: string;
  positionTitle: string;
  positionDesc: string;
  thisFieldList: positionRequirementInterface[] | null;
  thisEdication: education_category;
  thisLevel: school_level;
  thisCategory: string;
  thisSubCategory: string;
  thisCoursesId: string[];
  thisCourses: course[];
  thisRequirementList:
    | {
        user_detail_category_id: string;
        user_detail_subcategory_id: string;
        position_requirement: positionRequirementInterface[];
        required: boolean;
      }[]
    | null;
  filteredFields: DocumentFieldsInterface[] | null;
  chooseFields: boolean;
  thisField: string;
  isFieldRequired: boolean;
  isRequirementRequired: boolean;
  courseLength: number;
  formSteps: {
    ONE: boolean;
    TWO: boolean;
    THREE: boolean;
  };
  modalAction: false | "CREATE_POSITION" | "UPDATE_POSITION";
  setPositionId: React.Dispatch<React.SetStateAction<string>>;
  setPositionTitle: React.Dispatch<React.SetStateAction<string>>;
  setPositionDesc: React.Dispatch<React.SetStateAction<string>>;
  setThisCategory: React.Dispatch<React.SetStateAction<string>>;
  setThisSubCategory: React.Dispatch<React.SetStateAction<string>>;
  setThisFieldList: React.Dispatch<
    React.SetStateAction<positionRequirementInterface[] | null>
  >;
  setThisRequirementList: React.Dispatch<
    React.SetStateAction<
      | {
          user_detail_category_id: string;
          user_detail_subcategory_id: string;
          position_requirement: positionRequirementInterface[];
          required: boolean;
        }[]
      | null
    >
  >;
  setFormSteps: React.Dispatch<
    React.SetStateAction<{
      ONE: boolean;
      TWO: boolean;
      THREE: boolean;
      FOUR: boolean;
    }>
  >;
  setModalAction: React.Dispatch<
    React.SetStateAction<false | "CREATE_POSITION" | "UPDATE_POSITION">
  >;
  setFilteredFields: React.Dispatch<
    React.SetStateAction<DocumentFieldsInterface[] | null>
  >;
  setThisField: React.Dispatch<React.SetStateAction<string>>;
  setIsFieldRequired: React.Dispatch<React.SetStateAction<boolean>>;
  setIsRequirementRequired: React.Dispatch<React.SetStateAction<boolean>>;
  setCourseLength: React.Dispatch<React.SetStateAction<number>>;
  setChooseFields: React.Dispatch<React.SetStateAction<boolean>>;
  setStepState: React.Dispatch<
    React.SetStateAction<{
      ONE: boolean;
      TWO: boolean;
      THREE: boolean;
      FOUR: boolean;
    }>
  >;
  document: DocumentReducerInterface;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  getPositions: Function;
  cleanStates: () => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const StepThree = (props: StepThreeProps) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);
  const [addNewRequirement, setAddNewRequirement] = useState<boolean>(false);

  useEffect(() => {
    const randomTitle = (TYPE: "TITLE" | "DESCRIPTION" = "TITLE"): string => {
      let name: string = "Position for";
      props.thisCourses.forEach((item, index) => {
        if (index === 0) {
          name += " " + capitalizeFirstLetter(item.course_name);
        } else if (index === props.thisCourses.length - 1) {
          name += " and " + capitalizeFirstLetter(item.course_name);
        } else {
          name += ", " + capitalizeFirstLetter(item.course_name);
        }
      });

      if (TYPE === "DESCRIPTION") {
        name +=
          " - " + capitalizeFirstLetter(props.thisLevel.school_level_name);
        name +=
          " in " +
          capitalizeFirstLetter(props.thisEdication.education_category_title);
      }

      return name;
    };
    if (!props.document.documentCategory) {
      props.getDocumentCategory();
    } else if (!props.document.documentSubCategory) {
      props.getDocumentSubCategory();
    } else if (!props.document.documentFields) {
      props.getDocumentFields();
    } else {
      setLoading(false);
    }

    if (props.courseLength !== props.thisCourses.length) {
      props.setCourseLength(props.thisCourses.length);
      props.setPositionTitle(randomTitle());
      props.setPositionDesc(randomTitle("DESCRIPTION"));
    }
  }, [props, props.courseLength]);

  const removeField = (
    category_id: string,
    sub_category_id: string,
    field_id: string
  ) => {
    if (window.confirm("Are you sure you want to remove this document?")) {
      if (props.thisRequirementList) {
        let newArray = [...props.thisRequirementList];
        let index = props.thisRequirementList.findIndex(
          (item) =>
            item.user_detail_category_id === category_id &&
            item.user_detail_subcategory_id === sub_category_id
        );
        if (index >= 0) {
          if (props.thisRequirementList.length === 1) {
            console.log("Setting");
            props.setThisRequirementList([]);
          } else {
            if (newArray[index].position_requirement.length === 1) {
              newArray.splice(index, 1);
            } else {
              newArray[index].position_requirement = newArray[
                index
              ].position_requirement.filter(
                (item) => item.category_subcategory_data_id !== field_id
              );
            }
            props.setThisRequirementList(newArray);
          }
        }
      }
    }
  };

  const removeQualification = (
    category_id: string,
    sub_category_id: string
  ) => {
    if (window.confirm("Are you sure you want to remove this qualification?")) {
      if (props.thisRequirementList) {
        let newArray = [...props.thisRequirementList];
        let index = props.thisRequirementList.findIndex(
          (item) =>
            item.user_detail_category_id === category_id &&
            item.user_detail_subcategory_id === sub_category_id
        );
        if (index >= 0) {
          if (newArray.length === 1) {
            props.setThisRequirementList([]);
          } else {
            newArray = newArray.filter(
              (item) => item.user_detail_subcategory_id !== sub_category_id
            );
            props.setThisRequirementList(newArray);
          }
        }
      }
    }
  };

  const hasSubCategory = (_ID: string): boolean => {
    console.log("called");
    if (props.document.documentSubCategory) {
      if (
        props.document.documentSubCategory.find(
          (item) => item.category_id === _ID
        )
      ) {
        return true;
      }
    }
    return false;
  };

  const getSubCategoryById = (categoryId: string, subCategoryId: string) => {
    let result: DocumentSubCategoryInterface | undefined = undefined;
    if (props.document.documentSubCategory) {
      result = props.document.documentSubCategory.find(
        (item) =>
          item.category_id === categoryId &&
          item.user_detail_subcategory_id === subCategoryId
      );
    }
    return result;
  };

  const getFieldById = (
    categoryId: string,
    subCategoryId: string | null,
    field_id: string
  ): DocumentFieldsInterface | undefined => {
    let result: DocumentFieldsInterface | undefined = undefined;
    if (props.document.documentFields) {
      result = props.document.documentFields.find(
        (item) =>
          item.category_id === categoryId &&
          (item.subcategory_id === subCategoryId ||
            item.subcategory_id === null) &&
          item._id === field_id
      );
    }
    console.log({ result });
    return result;
  };

  const FieldsRequirementTable = (
    RequirementData: {
      user_detail_category_id: string;
      user_detail_subcategory_id: string;
      position_requirement: positionRequirementInterface[];
      required: boolean;
    }[]
  ): PositionRequirementsFieldInterface[] | [] => {
    let requirementTemp: PositionRequirementsFieldInterface[] = [];
    for (let p in RequirementData) {
      let category = RequirementData[p];
      let _sub_category = getSubCategoryById(
        RequirementData[p].user_detail_category_id,
        RequirementData[p].user_detail_subcategory_id
      );
      if (category.position_requirement.length > 0) {
        for (let d in category.position_requirement) {
          let _field = getFieldById(
            RequirementData[p].user_detail_category_id,
            RequirementData[p].user_detail_subcategory_id,
            category.position_requirement[d].category_subcategory_data_id
          );
          if (
            !requirementTemp.find(
              (item) =>
                item.category_id ===
                  RequirementData[p].user_detail_category_id &&
                item.sub_category_id ===
                  RequirementData[p].user_detail_subcategory_id &&
                item.field_id ===
                  category.position_requirement[d].category_subcategory_data_id
            )
          ) {
            requirementTemp.push({
              category_id: RequirementData[p].user_detail_category_id,
              category_name: props.document.documentCategory
                ? props.document.documentCategory.filter(
                    (item) =>
                      item.user_detail_category_id ===
                      RequirementData[p].user_detail_category_id
                  ).length > 0
                  ? props.document.documentCategory.filter(
                      (item) =>
                        item.user_detail_category_id ===
                        RequirementData[p].user_detail_category_id
                    )[0].title
                  : ""
                : "",
              sub_category_id: category.user_detail_subcategory_id,
              sub_category_name: _sub_category ? _sub_category.title : "",
              sub_category_required: RequirementData[p].required,
              field_id:
                category.position_requirement[d].category_subcategory_data_id,
              field_name: _field ? _field.title : "",
              required: category.position_requirement[d].required,
              isAnyField: false,
            });
          }
        }
      } else {
        if (
          !requirementTemp.find(
            (item) =>
              item.category_id === RequirementData[p].user_detail_category_id &&
              item.sub_category_id ===
                RequirementData[p].user_detail_subcategory_id
          )
        ) {
          requirementTemp.push({
            category_id: RequirementData[p].user_detail_category_id,
            category_name: props.document.documentCategory
              ? props.document.documentCategory.filter(
                  (item) =>
                    item.user_detail_category_id ===
                    RequirementData[p].user_detail_category_id
                ).length > 0
                ? props.document.documentCategory.filter(
                    (item) =>
                      item.user_detail_category_id ===
                      RequirementData[p].user_detail_category_id
                  )[0].title
                : ""
              : "",
            sub_category_id: category.user_detail_subcategory_id,
            sub_category_name: _sub_category ? _sub_category.title : "",
            sub_category_required: RequirementData[p].required,
            field_id: "",
            field_name: "",
            required: false,
            isAnyField: true,
          });
        }
      }
    }
    return requirementTemp;
  };

  // const QualificationRequirementTable = (
  //   RequirementData: {
  //     user_detail_category_id: string;
  //     user_detail_subcategory_id: string;
  //     position_requirement: positionRequirementInterface[];
  //     required: boolean;
  //   }[]
  // ): PositionRequirementsQualificationInterface[] | [] => {
  //   let requirementTemp: PositionRequirementsQualificationInterface[] = [];
  //   let fields: DocumentFieldsInterface[] = [];
  //   let subIds: string[] = [],
  //     fieldIds: string[] = [];
  //   for (let p in RequirementData) {
  //     let category = RequirementData[p];
  //     for (let d in category.position_requirement) {
  //       let _field = getFieldById(
  //         RequirementData[p].user_detail_category_id,
  //         RequirementData[p].user_detail_subcategory_id,
  //         category.position_requirement[d].category_subcategory_data_id
  //       );
  //       if (_field && !fieldIds.includes(_field._id)) {
  //         fields.push(_field);
  //         fieldIds.push(_field._id);
  //       }
  //     }
  //     let _sub_category = getSubCategoryById(
  //       RequirementData[p].user_detail_category_id,
  //       RequirementData[p].user_detail_subcategory_id
  //     );
  //     let _category = props.document.documentCategory
  //       ? props.document.documentCategory.find(
  //           (item) =>
  //             item.user_detail_category_id ===
  //             RequirementData[p].user_detail_category_id
  //         )
  //       : undefined;

  //     if (
  //       _category &&
  //       _sub_category &&
  //       !subIds.includes(_sub_category.user_detail_subcategory_id)
  //     ) {
  //       requirementTemp.push({
  //         category_id: _category.user_detail_category_id,
  //         category_name: _category.title,
  //         sub_category_id: _sub_category.user_detail_subcategory_id,
  //         sub_category_name: _sub_category.title,
  //         fields: fields,
  //         required: RequirementData[p].required,
  //       });
  //     }

  //     fields = [];
  //     fieldIds = [];
  //   }
  //   subIds = [];
  //   return requirementTemp;
  // };

  const fieldTableData = () => {
    if (!props.thisRequirementList || !props.thisRequirementList.length) {
      return [
        <tr key={4003433} className="border-b border-gray-200">
          <td className="p-3 text-center whitespace-nowrap" colSpan={4}>
            No Field Found
          </td>
        </tr>,
      ];
    }

    const data = FieldsRequirementTable(props.thisRequirementList);
    return data.map((item, index) => (
      <tr
        key={index}
        className="border-b border-gray-200 hover:bg-gray-100 group"
      >
        <td className="px-3 py-3 text-left">{item.category_name}</td>
        <td className="px-5 py-3 text-left">{item.sub_category_name}</td>
        <td className="px-5 py-3 text-left">
          {item.sub_category_required ? "Required" : "Optional"}
        </td>
        <td
          className={`px-5 py-3 ${item.isAnyField ? "text-left" : "text-left"}`}
          colSpan={item.isAnyField ? 2 : 0}
        >
          {item.isAnyField ? "Any field" : item.field_name}
        </td>
        {!item.isAnyField && (
          <td className="px-5 py-3 text-left">
            {item.required ? "Required" : "Optional"}
          </td>
        )}
        <td className="px-5 py-3">
          <div className="flex justify-end">
            <div
              onClick={() => {
                !loading &&
                  (item.isAnyField
                    ? removeQualification(
                        item.category_id,
                        item.sub_category_id
                      )
                    : removeField(
                        item.category_id,
                        item.sub_category_id,
                        item.field_id
                      ));
              }}
              className={`bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 ${
                loading ? "cursor-not-allowed" : "cursor-pointer"
              }`}
            >
              <MdDelete className="text-3xl text-red-700" />
            </div>
          </div>
        </td>
      </tr>
    ));
  };

  const finish = () => {
    if (!props.positionTitle || !props.positionTitle.length) {
      return setFormError({
        element: "position_title",
        msg: "Position title is required",
      });
    }
    if (!props.positionDesc || !props.positionDesc.length) {
      return setFormError({
        element: "position_desc",
        msg: "Position description is required",
      });
    }
    if (!props.thisRequirementList || !props.thisRequirementList.length) {
      return setFormError({
        element: "position_requirement",
        msg: "Document requirement(s) is required",
      });
    }

    props.setFormSteps({
      ONE: false,
      TWO: false,
      THREE: false,
      FOUR: true,
    });
    props.modalAction === "CREATE_POSITION" &&
      props.setStepState({
        ONE: true,
        TWO: true,
        THREE: true,
        FOUR: false,
      });
  };

  if (loading) {
    return <Loading />;
  }
  return (
    <section className="mx-4 my-3">
      <div className="flex flex-col mb-5">
        <span className="text-sm text-gray-600 font-bold mb-1">
          Position title
        </span>
        <input
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            props.setPositionTitle(e.target.value);
            setFormError(null);
          }}
          type="text"
          value={props.positionTitle}
          className={`input-white border-2 ${
            formError !== null && formError.element === "position_title"
              ? "border-red-500"
              : "border-gray-500"
          }`}
          disabled={loading}
        />
        {formError !== null && formError.element === "position_title" && (
          <span className="text-red-600 font-bold text-xs">
            {formError.msg}
          </span>
        )}
      </div>
      <div className="flex flex-col mb-5">
        <span className="text-sm text-gray-600 font-bold mb-1">
          Position description
        </span>
        <textarea
          onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => {
            props.setPositionDesc(e.target.value);
            setFormError(null);
          }}
          value={props.positionDesc}
          className={`input-white border-2 ${
            formError !== null && formError.element === "position_desc"
              ? "border-red-500"
              : "border-gray-500"
          }`}
          disabled={loading}
        ></textarea>
        {formError !== null && formError.element === "position_desc" && (
          <span className="text-red-600 font-bold text-xs">
            {formError.msg}
          </span>
        )}
      </div>

      {/* ============================== DISPLAYING SELECTED REQUIREMENTS ================================= */}
      {
        <section className="bg-white border-gray-300 rounded p-2 my-2">
          <div className="flex items-center justify-between gap-2 mb-3">
            <span className="text-lg font-semibold">List of Requirements:</span>
            <button
              className="btn btn-blue"
              onClick={() => {
                setAddNewRequirement(true);
                formError &&
                  formError.element === "position_requirement" &&
                  setFormError(null);
              }}
            >
              Add requirements
            </button>
          </div>
          {props.thisRequirementList && props.thisRequirementList.length > 0 ? (
            <Table
              columns={[
                {
                  columnTitle: "Category",
                  alignText: "left",
                  padding: "p-3",
                },
                {
                  columnTitle: "Qualification",
                  alignText: "left",
                  padding: "p-3",
                },
                {
                  columnTitle: "Qualification Status",
                  alignText: "left",
                  padding: "p-3",
                },
                {
                  columnTitle: "Field",
                  alignText: "left",
                  padding: "p-3",
                },
                {
                  columnTitle: "Field Status",
                  alignText: "left",
                  padding: "p-3",
                },
                { columnTitle: "", alignText: "left", padding: "p-3" },
              ]}
              rows={fieldTableData}
              pagination={false}
              search={false}
              hideColumns={false}
              thColor="blue"
            />
          ) : (
            <div
              className={`px-3 py-7 text-xl rounded my-1 text-center ${
                formError && formError.element === "position_requirement"
                  ? "border-red-700 text-red-800 border-2"
                  : "border"
              }`}
            >
              {formError && formError.element === "position_requirement"
                ? formError.msg
                : "No Requirements available"}
            </div>
          )}
        </section>
      }
      {/* ============================ END DISPLAYING SELECTED REQUIREMENTS ============================== */}

      <div className="flex items-center justify-between">
        <button
          className="btn bg-gray-500 text-gray-50 mb-2 animate__animated animate__fadeIn focus:outline-none"
          onClick={() =>
            props.setFormSteps({
              ONE: false,
              TWO: true,
              THREE: false,
              FOUR: false,
            })
          }
        >
          Back
        </button>
        <button
          className="btn btn-blue mb-2 animate__animated animate__fadeIn focus:outline-none"
          onClick={() => finish()}
          disabled={loading}
        >
          Review
        </button>
      </div>

      {/* Add requirements */}
      {addNewRequirement && (
        <AddRequirements
          formError={formError}
          thisCategory={props.thisCategory}
          thisRequirementList={props.thisRequirementList}
          documentFields={props.document.documentFields}
          setThisCategory={props.setThisCategory}
          setThisSubCategory={props.setThisSubCategory}
          setAddNewRequirement={setAddNewRequirement}
          setThisFieldList={props.setThisFieldList}
          setThisRequirementList={props.setThisRequirementList}
          setFilteredFields={props.setFilteredFields}
          hasSubCategory={hasSubCategory}
          documentCategory={props.document.documentCategory}
          thisSubCategory={props.thisSubCategory}
          documentSubCategory={props.document.documentSubCategory}
          chooseFields={props.chooseFields}
          filteredFields={props.filteredFields}
          loading={loading}
          setChooseFields={props.setChooseFields}
          isFieldRequired={props.isFieldRequired}
          thisFieldList={props.thisFieldList}
          setThisField={props.setThisField}
          setIsFieldRequired={props.setIsFieldRequired}
          setFormError={setFormError}
          setIsRequirementRequired={props.setIsRequirementRequired}
          isRequirementRequired={props.isRequirementRequired}
        />
      )}
    </section>
  );
};

const mapStateToProps = ({
  document,
}: StoreState): {
  document: DocumentReducerInterface;
} => {
  return {
    document,
  };
};

export default connect(mapStateToProps, {
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
  getPositions,
  PushPopupAlert,
})(StepThree);
